}
.NumberView__value {
  font-size: 32px;
  line-height: 32px;
  color: var(--text_primary);
  white-space: nowrap;
}

.NumberView__value:after {
  display: inline-block;
  width: 27px;
  height: 27px;
  content: "";
  background-image: url(https://vk.com);
  background-position: 50%;
  background-repeat: no-repeat;
  margin-left: 8px;
  vertical-align: -4px;
}
.NumberView {
  border-radius: 2px;
  background: #fff;
  box-shadow: 0 0 8px 1px rgba(0,0,0,.04),0 8px 20px 0 rgba(0,0,0,.07);
  box-sizing: border-box;
  padding: 10px;

}
.feedу  {
  color: #1ABC9C;
}

.TabbarItem--selected {
  color:white;
}


.TabbarItem--selected.feedу {
      color: white;
  background-color: #1ABC9C;

  border-radius: 5px 5px 0px 0px;
  transition: all 120ms linear;
  position: relative;
  transform: translateY(0);
}

.TabbarItem .Counter__in {
    font-size: 10px;
    font-weight: 500;
    white-space: nowrap;
}

body, html {
    width: 100%;
    height: 100%;
    position: relative;
}

.name {
  text-align: center;
  position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    color: white;
    text-transform: uppercase;
    font-size: 21px;
    font-weight: 300;
    border-radius: 0 0 4px 4px;
    padding-top: 2px;
    transition: all .1s linear;
    background: #2c0c16ce!important;
}
#idgame .Card .Card__in {
  height: 130px;
}

#idgame .Card:active {
  transform: scale(.9);
  border:0px;
}

