  transition: all .1s linear;
}
#idgame {
  padding-top: 16px;
}

#promobutton {
  color:#1ABC9C;
}
.videoButton {
  margin-bottom: 10px;
    background: #2c0c16ce;
    border-radius: 2px;
    padding: 20px 10px;
    position: relative;
    overflow: hidden;
    color: #F39C12;
    transition: transform .1s linear,-webkit-transform .1s linear;
}
.videoButton:active {
  transform: scale(.9);
}
.shopButton:active {
  transform: scale(.9);
}

.videoButton .title {
  float: left;
  display: inline-block;
text-transform: uppercase;
font-weight: 400;
font-size: 21px;
margin-left: 57px;
transform-origin: 50% 50%;
}

#videobonus .title {
  float: right;
  display: inline-block;
text-transform: uppercase;
font-weight: 400;
font-size: 21px;
margin-right: 49px;
transform-origin: 50% 50%;
}

.videoButton .icon {
  position: absolute;
    top: 2px;
    left: -23px;
}

#videobonus .icon {
  position: absolute;
    top: 2px;
    left: calc(100% - 59px);
}

.videoButton:active .icon {
      transform: translate(-12px,12px) scale(.95);
}

#videobonus:active .icon {
      transform: translate(12px,12px) scale(.95);
}

.shopButton:active .icon {
  transform: translate(-12px,12px) scale(.95);
}

.shopButton, .profile, .shopButton, .icon {
    transition: -webkit-transform .1s linear;
    transition: transform .1s linear;
    transition: transform .1s linear,-webkit-transform .1s linear;
  }

  .videoButton, .profile, .shopButton, .icon {
