}

.bottom {
    display: -webkit-flex;
    display: flex;
    border-top: 2px solid #1abc9c;
    background: #2c0c16ce;
    padding: 10px 0;
    color: #ecf0f1;
}

.bottom .fund {
    width: 49.5%;
    text-align: center;
}

.bottom .fund .title {
    font-weight: 200;
    font-size: 12px;
    text-transform: uppercase;
}

.bottom .fund .count {
    margin-top: 3px;
    font-weight: 600;
    font-size: 16px;
}

.bottom .cLine {
    width: 1%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
}

.bottom .end, .bottom .fund {
    width: 49.5%;
    text-align: center;
}

.paddingWrapper {
    padding: 0 12px;
}
.infoBlock {
    border-radius: 4px;
    overflow: hidden;
    margin-top: 10px;
    color: #ecf0f1;
}
.infoBlock .top {
    padding: 13px 10px 20px 15px;
    background: #2c0c16ce;
    position: relative;
}
.infoBlock .top:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #2c0c16ce;
    opacity: .8;
    z-index: 0;
}

.infoBlock .top .header {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
    position: relative;
    z-index: 1;
}

.infoBlock .top .header .title {
    font-size: 21px;
    font-weight: 400;
}

.infoBlock .top .text {
    font-weight: 300;
    margin-top: 10px;
    position: relative;
    z-index: 1;
}

.bottom .cLine div {
    width: 100%;
    max-width: 1px;
    height: 20px;
    background: hsla(0,0%,100%,.5);
    border-radius: 8px;
}

.bottom .end .title, .Rating .infoBlock .bottom .fund .title {
    font-weight: 200;
    font-size: 12px;
    text-transform: uppercase;
}

.bottom .end .title, .Rating .infoBlock .bottom .fund .title {
    font-weight: 200;
    font-size: 12px;
    text-transform: uppercase;
}

.bottom .end .count, .Rating .infoBlock .bottom .fund .count {
    margin-top: 3px;
    font-weight: 600;
    font-size: 16px;
}

.vkuiTabbar--ios.vkuiTabbar--shadow:before {
    position: absolute;
    bottom: 100%;
    left: 0;
    width: 100%;
    height: 1px;
    -webkit-transform-origin: center bottom;
    transform-origin: center bottom;
    content: "";
}

.vkuiPanel:after {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    z-index: 1;
    content: "";
}


.vkuiTabbar--ios.vkuiTabbar--shadow::before {
  background: transparent;
}

