@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
  margin:0px;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  --background_content: #2c0c16ce !important;
  --header_background:#2c0c16ce !important;
  --header_alternate_background:#2c0c16ce !important;
  --header_text:white !important;
  --font-tt: "TT Commons",-apple-system,system-ui,Helvetica Neue,Roboto,sans-serif !important;
  --header_tint:white !important;
  --thin-border:0px !important;
  --background_suggestions:transparent !important;
  --accent:#1ABC9C !important;
  --tabbar_background:#2c0c16ce !important;
  --search_bar_background:#2c0c16ce !important;
  --search_bar_field_background:#2c0c16ce !important;
  --ios-easing: cubic-bezier(0.36,0.66,0.04,1) !important;
    --android-easing: cubic-bezier(0.4,0,0.2,1) !important;
}
.custom-snackbar {
  background-color: #2c0c16ce !important;
  color: #fff !important;
  border-radius: 8px !important;
}

.custom-snackbar .vkuiSnackbar__content {
  color: #fff !important;
}


#root {
  width: 100%;
  height: 100%;
  position: relative;
}

.verticalText {
    display: inline-block;
    vertical-align: middle;
}

.vkIcon {
    display: inline-block;
    margin-left: 3px;
    vertical-align: middle;
}

.vkuiModalPage--android .vkuiModalPage__in-wrap, .vkuiModalPage--vkcom .vkuiModalPage__in-wrap {
    transition: -webkit-transform 0.25s cubic-bezier(.4,0,.2,1);
     transition: transform 0.25s cubic-bezier(.4,0,.2,1);
     transition: transform 0.25s cubic-bezier(.4,0,.2,1), -webkit-transform 0.25s cubic-bezier(.4,0,.2,1);
     transition: transform 0.25s cubic-bezier(.4,0,.2,1),-webkit-transform 0.25s cubic-bezier(.4,0,.2,1);
     transition: -webkit-transform 0.25s var(--android-easing);
     transition: transform 0.25s var(--android-easing);
     transition: transform 0.25s var(--android-easing), -webkit-transform 0.25s var(--android-easing);
     transition: transform 0.25s var(--android-easing),-webkit-transform 0.25s var(--android-easing);
}


.vkuiModalCard--android .vkuiModalCard__in, .vkuiModalCard--vkcom .vkuiModalCard__in {
  transition: -webkit-transform 0.25s cubic-bezier(.4,0,.2,1);
   transition: transform 0.25s cubic-bezier(.4,0,.2,1);
   transition: transform 0.25s cubic-bezier(.4,0,.2,1), -webkit-transform 0.25s cubic-bezier(.4,0,.2,1);
   transition: transform 0.25s cubic-bezier(.4,0,.2,1),-webkit-transform 0.25s cubic-bezier(.4,0,.2,1);
   transition: -webkit-transform 0.25s var(--android-easing);
   transition: transform 0.25s var(--android-easing);
   transition: transform 0.25s var(--android-easing), -webkit-transform 0.25s var(--android-easing);
   transition: transform 0.25s var(--android-easing),-webkit-transform 0.25s var(--android-easing);
 }

 .desc {
    font-weight: 300;
    color: rgb(236, 240, 241);
}

.desc b {
    font-weight: 300;
    color: #bdc3c7;
}

.myPromo .vkuiSimpleCell__children {
    color: #ecf0f1!important;
    font-weight: 500;
    letter-spacing: .15em;
    font-size: 15px;
}

.myPromo .vkuiSimpleCell__description {
  margin-top: 0px;
}


.vkuiModalPage--android .vkuiModalPage__in, .vkuiModalPage--vkcom .vkuiModalPage__in {
    border-radius: 5px 5px 0 0;
}

.vkuiModalCard--android .vkuiModalCard__container, .vkuiModalCard--vkcom .vkuiModalCard__container {
    border-radius: 5px;
  }
